<template>
  <div v-if="!card.for_sale" class="card">
    <app-knife-image :image="card.image"></app-knife-image>
    <div class="card-body">
      <blockquote>
        <p class="card-text">{{card.content}}</p>
      </blockquote>
    </div>
  </div>
  <div v-else class="card bg-dark text-white">
    <div class="card-header">
      <h3 class="text-warning">For Sale
        <span v-if="card.price">:</span>
      </h3>
      <h3 class="text-warning">
        <span v-if="card.price">$</span>{{card.price}}</h3>
    </div>
    <app-knife-image :image="card.image"></app-knife-image>
    <div class="card-body">
      <blockquote>
        <p class="card-text">{{card.content}}</p>
        <a :href="mailToBody" target="_blank" class="btn btn-warning card-btn mt-3 mb-1">
          <i class="fa fa-envelope mr-2" aria-hidden="true"></i>Order My Knife</a>
      </blockquote>
    </div>
  </div>
</template>
<script>
import knifeImage from "../molecules/KnifeImage.vue";
export default {
  components: { appKnifeImage: knifeImage },
  props: ["card"],
  computed: {
    cardBody() {
      return `Hello Mathhew! I'd like to order the following knife from you:%0D%0A%0D%0A${
        this.card.content
      }`;
    },
    mailToBody() {
      let mailToHref = `mailto:carterknifeco@gmail.com?subject=Custom, ready-to-order knife purchase from carterknifeco.com&body=${
        this.cardBody
      }`;
      return mailToHref;
    }
  }
};
</script>
<style lang="scss" scoped>
.card {
  h3,
  h4 {
    margin: 0;
  }
  cursor: default;
  box-shadow: initial;
  &:hover {
    transform: none;
    box-shadow: initial;
  }
}
.card-body {
  min-height: 0;
  padding: 1rem 1.25rem;
  padding-bottom: 0;
  p {
    font-family: "Open Sans";
    margin-bottom: 0;
  }
}
.card-img {
  border-radius: 0;
}
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>


